@import url("https://fonts.googleapis.com/css2?family=Blaka+Hollow&family=Roboto:wght@400;900&display=swap");
.home {
  background-color: #000000;
  font-size: 4rem;
  min-height: 100vh;
  color: black;
  display: grid;
  grid-template-rows: 10%;
  background-image: url("./img.jpg");
}
.para {
  width: max-content;
  margin: auto;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
}
.homePage {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn {
  margin: auto;
  display: block;
  margin: 2rem 0;
  font-size: large;
  background-color: #b91c1c;
  color: white;
  height: 3rem;
  width: 14rem;
  border: none;
  border-radius: 10px;
  box-shadow: 1px 1px 3px black;
}
.btn:hover {
  background-color: #0f172a;
}
