.items {
  padding: 25px 0px 25px 25px;
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  height: 100%;
  justify-content: center;
}
.item {
  background-color: #404040;
  color: white;
  border-radius: 15px;
  height: 320px;
  border: 5px solid #27272a;
  width: 500px;
  display: flex;
  padding: 5px;
  gap: 15px;
  padding: 25px;
}
.img {
  border-radius: 25px;
  height: 300px;
  width: 200px;
}
.item:hover {
  background-color: #0f172a;
}
