.shopItem {
  background-color: #64748b;
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 35px;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
}
.item {
  border: 1px solid black;
  background-color: #262626;
  border-radius: 15px;
  padding: 25px;
  height: 55%;
  width: 60%;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.img {
  width: 300px;
  height: 400px;
}
.content {
  display: flex;
  gap: 15px;
}
.contentOne {
  font-size: 1.3rem;
}
.btn {
  display: block;
  margin: 2rem 0;
  font-size: large;
  background-color: #b91c1c;
  color: white;
  height: 3rem;
  width: 14rem;
  border: none;
  border-radius: 10px;
  box-shadow: 1px 1px 3px black;
}
.btn:hover {
  background-color: #991b1b;
}

.title {
  font-size: 1.5rem;
  font-weight: 700;
}
.svg {
  width: 5rem;
  border-radius: 15px;
  padding-left: 10px;
}
.svg:hover {
  background-color: #262626;
  fill: white;
}
