@import url("https://fonts.googleapis.com/css2?family=Blaka+Hollow&display=swap");

.nav {
  background-color: #0f172a;
  height: 100px;
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: "Blaka Hollow", sans-serif;
}
.link {
  display: flex;
  min-width: 20%;
  justify-content: space-between;
}
.link > * {
  text-decoration: none;
  color: white;
  font-size: 3rem;
}
.link > *:hover {
  color: red;
}

.badge {
  font-family: sans-serif;
  padding-left: 9px;
  padding-right: 9px;
  -webkit-border-radius: 9px;
  -moz-border-radius: 9px;
  border-radius: 9px;
}

.label_warning[href],
.badge_warning[href] {
  background-color: #c67605;
}
.CartCount {
  font-size: 12px;
  background: #ff0000;
  color: #fff;
  padding: 0 5px;
  vertical-align: top;
  margin-left: -10px;
}

.material_symbols_outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 48;
}
.svg:hover {
  fill: red;
}
