.img {
  height: 400px;
  width: 250px;
}
.main {
  background-color: #334155;
  font-family: sans-serif;
  min-height: 100vh;
}
.items {
  padding: 25px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}
.item {
  padding: 15px;
  height: 400px;
  width: 600px;
  background-color: #3f3f46;
  border: 5px solid #27272a;
  display: flex;
  gap: 15px;
}
.quantity {
  margin: 30px auto;
  display: flex;
  justify-content: space-between;
  width: 100px;
}
.itemNo {
  font-size: 3rem;
}
.content {
  display: flex;
  flex-direction: column;
}
.price {
  width: 400px;
  padding: 3px 0px 5px;
  margin: auto;
  font-size: 3rem;
  background-color: #27272a;
  border-radius: 15px;
  text-align: center;
}
.btn {
  margin: 0px;
  display: block;
  margin: 2rem 0;
  font-size: large;
  background-color: #b91c1c;
  color: white;
  height: 3rem;
  width: 14rem;
  border: none;
  border-radius: 10px;
  box-shadow: 1px 1px 3px black;
}
.btn:hover {
  background-color: #991b1b;
}
.down {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
